<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>栏目</el-col>
                <el-col style="width:56px">
                    <el-button @click="showAddCategoryDialog()" :plain="true" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>
        <el-table :border="true" :data="categoryList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="排序" width="70px" prop="orderNum">
                <template slot-scope="scope">
                    <el-input size="mini" style="text-align: center!important;" v-model="scope.row.orderNum" @change="order(scope.row.orderNum, scope.row.id)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="名称" prop="name"></el-table-column>
            <el-table-column label="类型" width="80px" :formatter="typeFormatter" prop="type"></el-table-column>
            <el-table-column label="操作" width="160px">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.type==1" @click="pageEdit(scope.row.id)" size="small" type="text">编辑内容</el-button>
                    <el-button @click="showEditCategoryDialog(scope.$index,scope.row)" size="small" type="text">编辑</el-button>
                    <el-button @click="doDeleteCategory(scope.$index, scope.row)" size="small" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加栏目" :visible.sync="addCategoryFormVisible">
            <el-form :model="addCategoryForm" :rules="rules" ref="addCategoryFormRef" label-width="80px">
                <el-form-item label="类型" prop="type">
                    <el-select v-model="addCategoryForm.type">
                        <el-option label="栏目" value="0" key="栏目"></el-option>
                        <el-option label="单网页" value="1" key="单网页"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="addCategoryForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="addCategoryFormVisible = false">取消</el-button>
                <el-button @click="doAddCategory">确定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑栏目" :visible.sync="editCategoryFormVisible">
            <el-form :model="editCategoryForm" :rules="rules" ref="editCategoryFormRef" label-width="80px">
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="editCategoryForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="editCategoryFormVisible = false">取消</el-button>
                <el-button @click="doEditCategory">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
export default {
    name:'null',
    data: function () {
        return {
            rules: {
                type:[
                    { required: true, message: '请选择类型', trigger: 'change' }
                ],
                name: [
                    { required: true, message: '请填写名称', trigger: 'blur' }
                ]
            },
            addCategoryForm:{
            },
            editCategoryFormVisible:false,
            addCategoryFormVisible:false,
            categoryMultipleSelection:[],
            categoryList:[],
            editCategoryForm:{}
        }
    },
    methods:{
        typeFormatter: function (row, column,cellValue) {
            if(cellValue==0){
                return "栏目"
            } else {
                return "单网页"
            }
        },
        pageEdit:function (id) {
            this.$router.push('/pageEdit/'+id);
        },
        order:function (value,id) {
            var self = this;
            axios.post('dayu/category/order',Qs.stringify({id:id,orderNum:value}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.searchCategory();
                        } else {

                        }
                    })
                    .catch(function (response) {

                            }
                    );
        },

        searchCategory:function(){
            var self = this;
            axios.post('dayu/category/list')
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.categoryList = response.data.list;
                    } else {
                        console.log("获取数据失败");
                    }
                })
                .catch(function (response) {
                    console.log(response);
                }
            );
        },

        doDeleteCategory:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/category/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.searchCategory();
                        } else {
                            alert("删除失败");
                        }
                    })
                    .catch(function (response) {
                        console.log(response);
                    }
                );
            }).catch(() => {
            });
        },

        showAddCategoryDialog:function(){
            var self = this;
            self.addCategoryFormVisible = true
        },

        doAddCategory:function(){
            var self = this;
            this.$refs["addCategoryFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/category/add',Qs.stringify(self.addCategoryForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.addCategoryFormVisible = false;
                                self.addCategoryForm = {};
                                self.searchCategory();
                                self.$message("添加成功")
                            } else {
                                self.$message("添加失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        },

        showEditCategoryDialog:function(index,row){
            var self = this;
            self.editCategoryFormVisible = true;
            self.editCategoryForm.name = row.name;
            self.editCategoryForm.id = row.id;
        },

        doEditCategory:function(){
            var self = this;
            this.$refs["editCategoryFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/category/edit',Qs.stringify(self.editCategoryForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.editCategoryFormVisible = false;
                                self.searchCategory();
                                self.$message("编辑成功")
                            } else {
                                self.$message("编辑失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        }
    },
    created:function() {
        var self = this;
        self.searchCategory();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



