<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>咨询</el-col>
                <el-col style="width:56px">
                </el-col>
            </el-row>
        </div>

        <el-table :border="true" :data="advisoryList" :stripe="true">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="移民需求">
                            <span>{{ props.row.immigrationRequest }}</span>
                        </el-form-item>
                        <el-form-item label="时间要求">
                            <span>{{ props.row.timeRequest }}</span>
                        </el-form-item>
                        <el-form-item label="投资预算">
                            <span>{{ props.row.priceRequest }}</span>
                        </el-form-item>
                        <el-form-item label="经商经验">
                            <span>{{ props.row.businessExperience }}</span>
                        </el-form-item>
                        <el-form-item label="海外经历">
                            <span>{{ props.row.overseasExperience }}</span>
                        </el-form-item>
                        <el-form-item label="可承受风险">
                            <span>{{ props.row.riskTolerance }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="姓名" prop="name"></el-table-column>
            <el-table-column label="电话" prop="phone"></el-table-column>
            <el-table-column label="意向国家" prop="country"></el-table-column>
            <el-table-column label="创建时间" :formatter="dateFormatter"  width="200px" prop="createTime"></el-table-column>

        </el-table>

        <el-pagination @current-change="handleAdvisoryPageChange" :current-page="advisoryCurrentPage" :page-size="advisoryPageSize" @size-change="handleAdvisoryPageSizeChange" :total="advisoryTotalCount"></el-pagination>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';

export default {
    name:'null',
    data: function () {
        return {
            advisoryPageSize:20,
            advisoryCurrentPage:0,
            advisoryTotalCount:0,
            advisoryList:[]
        }
    },
    methods:{


        dateFormatter: function (row, column,cellValue) {
            return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
        },

        SearchAdvisoryWithPage:function(){
            var self = this;
            axios.post('dayu/advisory/pageList',Qs.stringify({pageSize:self.advisoryPageSize,pageNo:self.advisoryCurrentPage}))
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.advisoryList = response.data.list;
                        self.advisoryTotalCount = response.data.totalRecord;
                    } else {
                        console.log("获取数据失败");
                    }
                })
                .catch(function (response) {
                    console.log(response);
                }
            );
        },

        handleAdvisoryPageSizeChange:function(size){
            var self = this;
            self.advisoryPageSize = size;
            self.SearchAdvisoryWithPage();
        },

        handleAdvisoryPageChange:function(currentPage){
            var self = this;
            self.advisoryCurrentPage = currentPage;
            self.SearchAdvisoryWithPage();
        }

    },
    created:function() {
        var self = this;
        self.SearchAdvisoryWithPage();
    }
}
</script>

<style>
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .el-pagination{padding: 10px;background-color: #eef1f6;border: 1px solid #e6ebf5;border-top: 0px;}
</style>



