<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>资讯</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="addNew" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>

        <el-table :border="true" :data="newList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="标题" prop="title"></el-table-column>
            <el-table-column label="状态" prop="status" width="80px">
                <template slot-scope="scope">
                    <el-tag v-if="scope.row.status==0" type="danger">编辑中</el-tag>
                    <el-tag v-if="scope.row.status==1" type="success">已发布</el-tag>
                </template>
            </el-table-column>

            <el-table-column label="创建时间" :formatter="dateFormatter"  width="170px" prop="createTime"></el-table-column>
            <el-table-column label="操作" width="180px">
                <template slot-scope="scope">
                    <el-button size="small" type="text" @click="recommend(scope.row.id,scope.row.title)" >推荐</el-button>
                    <el-button size="small" @click="editNew(scope.row.id)" type="text">编辑</el-button>
                    <el-button size="small"  @click="doDeleteNew(scope.$index, scope.row)" type="text">删除</el-button>
                    <el-button size="small" v-if="scope.row.status==0"  @click="up(scope.row.id)" type="text">发布</el-button>
                    <el-button size="small" v-if="scope.row.status==1"  @click="down(scope.row.id)" type="text">下架</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog
                title="推荐位"
                :visible.sync="recommendDialogVisible"
                >
            <el-transfer v-model="recommendPosition" :titles="['推荐位', '已选']" :data="positionList"></el-transfer>
            <span slot="footer" class="dialog-footer">
                <el-button @click="recommendDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="doRecommend">确 定</el-button>
            </span>
        </el-dialog>

        <el-pagination @current-change="handleNewPageChange" :current-page="newCurrentPage" :page-size="newPageSize" @size-change="handleNewPageSizeChange" :total="newTotalCount"></el-pagination>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';

export default {
    name:'null',
    data: function () {
        return {
            newPageSize:20,
            newCurrentPage:0,
            newTotalCount:0,
            newList:[],
            positionList:[],
            recommendDialogVisible:false,
            recommendPosition:[],
            recommendTitle:"",
            recommendId:0,
        }
    },
    methods:{

        up:function (id) {
            var self = this;
            axios.post('dayu/new/up',Qs.stringify({id:id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchNewWithPage();
                            self.$message("发布成功")
                        } else {
                            self.$message("发布失败")
                        }
                    })
                    .catch(function (response) {
                        self.$message("发布失败")
                            }
                    );
        },

        top:function (id) {
            var self = this;
            axios.post('dayu/new/top',Qs.stringify({id:id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchNewWithPage();
                            self.$message("置顶成功")
                        } else {
                            self.$message("置顶失败")
                        }
                    })
                    .catch(function (response) {
                                self.$message("置顶失败")
                            }
                    );
        },

        notTop:function (id) {
            var self = this;
            axios.post('dayu/new/notTop',Qs.stringify({id:id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchNewWithPage();
                            self.$message("取消成功")
                        } else {
                            self.$message("取消失败")
                        }
                    })
                    .catch(function (response) {
                                self.$message("取消失败")
                            }
                    );
        },

        down:function (id) {
            var self = this;
            axios.post('dayu/new/down',Qs.stringify({id:id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchNewWithPage();
                            self.$message("下架成功")
                        } else {
                            self.$message("下架失败")
                        }
                    })
                    .catch(function (response) {
                        self.$message("下架失败")
                            }
                    );
        },

        addNew: function () {
            this.$router.push('/addNew');
        },

        editNew: function (id) {
            this.$router.push('/editNew/'+id);
        },

        dateFormatter: function (row, column,cellValue) {
            return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
        },

        SearchNewWithPage:function(){
            var self = this;
            axios.post('dayu/new/pageList',Qs.stringify({pageSize:self.newPageSize,pageNo:self.newCurrentPage}))
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.newList = response.data.list;
                        self.newTotalCount = response.data.totalRecord;
                    } else {
                        console.log("获取数据失败");
                    }
                })
                .catch(function (response) {
                    console.log(response);
                }
            );
        },

        doDeleteNew:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/new/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchNewWithPage();
                            self.$message('删除成功');
                        } else {
                            self.$message('删除失败');
                        }
                    })
                    .catch(function (response) {
                        self.$message('删除失败');
                    }
                );
            }).catch(() => {
            });
        },

        handleNewPageSizeChange:function(size){
            var self = this;
            self.newPageSize = size;
            self.SearchNewWithPage();
        },

        handleNewPageChange:function(currentPage){
            var self = this;
            self.newCurrentPage = currentPage;
            self.SearchNewWithPage();
        },

        recommend:function(id,name){
            this.recommendDialogVisible = true;
            this.selectedPosition(id);
            this.recommendTitle = name;
            this.recommendId = id;
        },

        doRecommend:function () {
            var self = this;
            axios.post('dayu/recommend/recommend',Qs.stringify({positionList:self.recommendPosition,contentId:self.recommendId,title:self.recommendTitle,type:"new"}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.$message("推荐成功");
                            self.recommendDialogVisible = false;
                        } else {
                            self.$message("推荐失败");
                        }
                    })
                    .catch(function (response) {
                                self.$message("推荐失败");
                            }
                    );
        },

        selectedPosition:function (contendId) {
            var self = this;
            self.recommendPosition = [];
            axios.post('dayu/recommend/positionList',Qs.stringify({id:contendId,type:"new"}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.recommendPosition = response.data.list;
                        } else {
                            self.$message("获取数据失败");
                        }
                    })
                    .catch(function (response) {
                                self.$message("获取数据失败");
                            }
                    );
        },
        searchPosition:function(){
            var self = this;
            self.positionList = [];
            axios.post('dayu/recommendPosition/list?type=new')
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            for (var i=0;i<response.data.list.length;i++){
                                self.positionList.push({
                                    key:response.data.list[i].id,
                                    label:response.data.list[i].name,
                                })
                            }
                        } else {
                            self.$message("获取数据失败");
                        }
                    })
                    .catch(function (response) {
                                self.$message("获取数据失败");
                            }
                    );
        }

    },
    created:function() {
        var self = this;
        self.SearchNewWithPage();
        self.searchPosition();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .el-pagination{padding: 10px;background-color: #eef1f6;border: 1px solid #e6ebf5;border-top: 0px;}
</style>



