<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>
                    <el-select clearable size="mini" v-model.number="currentGroupId" placeholder="请选择">
                        <el-option label="未分组" :value="0"></el-option>
                        <el-option
                                v-for="group in groupList"
                                :key="group.id"
                                :label="group.name"
                                :value="group.id">
                        </el-option>
                    </el-select>
                    <el-button size="mini" @click="showGroupDialog" type="text" style="margin-left: 10px;">编辑分组</el-button>
                </el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="addImage" size="mini" type="success">上传</el-button>
                </el-col>
            </el-row>
        </div>


        <div style="display: flex;flex-wrap:wrap;">
            <el-card
                    v-for="image in imageList" :key="image.id"
                    :body-style="{ padding: '0px' }"
                    style="text-align: center;width: 200px;margin-bottom: 10px;margin-right: 10px;">
                <img :src="image.url" class="image" style="height: 100px;">
                <div style="padding: 5px;background-color:#eef1f6 ;text-align: left">
                    <span style="font-size: 12px;height: 12px;line-height: 12px;width:100px; overflow: hidden;display: inline-block">{{image.name}}</span>

                        <i @click="doDelete(image.id)" style="cursor: pointer;float: right;color: red; margin-left: 10px;margin-top: 3px" class="el-icon-circle-close"></i>
                        <i
                                @click="editImage(image)"
                                style="cursor: pointer;float: right;margin-left: 10px;margin-top: 3px"
                           class="el-icon-edit"
                        ></i>

                </div>
            </el-card>
        </div>

        <el-dialog title="分组管理" :visible.sync="imageGroupVisible">



        </el-dialog>

        <el-dialog title="编辑" :visible.sync="editImageFormVisible">

            <el-select style="margin-bottom: 20px;" size="mini" v-model.number="editImageForm.categoryId" placeholder="请选择">
                <el-option label="未分组" :value="0"></el-option>
                <el-option
                        v-for="group in groupList"
                        :key="group.id"
                        :label="group.name"
                        :value="group.id">
                </el-option>
            </el-select>

            <el-input size="mini" placeholder="文件名 " v-model="editImageForm.name"></el-input>

            <div class="dialog-footer" slot="footer">
                <el-button @click="editImageFormVisible = false">取消</el-button>
                <el-button @click="doEditImage">确定</el-button>
            </div>
        </el-dialog>

        <el-dialog title="上传照片" :visible.sync="addImageFormVisible">

            <el-select style="margin-bottom: 20px;" size="mini" v-model.number="addImageForm.category" placeholder="请选择">
                <el-option label="未分组" :value="0"></el-option>
                <el-option
                        v-for="group in groupList"
                        :key="group.id"
                        :label="group.name"
                        :value="group.id">
                </el-option>
            </el-select>

            <el-upload
                    class="avatar-uploader"
                    action="http://up-z0.qiniu.com"
                    :show-file-list="false"
                    :on-error="handleError"
                    :on-success="handleAvatarSuccess"
                    :data="postData"
                    :before-upload="beforeAvatarUpload">
                <img v-if="addImageForm.picture" :src="addImageForm.picture" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>

            <div class="dialog-footer" slot="footer">
                <el-button @click="addImageFormVisible = false">取消</el-button>
                <el-button @click="doAddImage">确定</el-button>
            </div>
        </el-dialog>

        <el-pagination
                background
                layout="prev, pager, next"
                style="margin-top: 20px;"
                @current-change="handleImagePageChange"
                :current-page="imageCurrentPage"
                :page-size="imagePageSize"
                @size-change="handleImagePageSizeChange"
                :total="imageTotalCount"></el-pagination>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';

export default {
    name:'null',
    data: function () {
        return {
            imagePageSize:30,
            imageCurrentPage:0,
            imageTotalCount:0,
            imageList:[],
            groupList:[],
            currentGroupId:"",
            addImageFormVisible:false,
            editImageFormVisible:false,
            imageGroupVisible:false,
            addImageForm:{
                category:0,
                picture:"",
                name:""
            },
            editImageForm:{
                categoryId:0,
                name:"",
                id:0
            },
            postData:{
                token:""
            }
        }
    },
    watch:{
        currentGroupId:function (val,oldVal) {
            this.SearchImageWithPage();
        }
    },
    methods:{
        addImage: function () {
            this.addImageFormVisible = true;
        },

        showGroupDialog:function () {
            this.imageGroupVisible = true;
        },

        doAddImage:function () {
            var self = this;
            axios.post('dayu/image/add',Qs.stringify({categoryId:self.addImageForm.category,url:self.addImageForm.picture,name:self.addImageForm.name,type:""}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchImageWithPage();
                            self.addImageFormVisible = false;
                            self.addImageForm = {
                                category:0,
                                picture:"",
                                name:""
                            }
                        } else {

                        }
                    })
                    .catch(function (response) {
                                self.$message("上传失败")
                            }
                    );
        },

        dateFormatter: function (row, column,cellValue) {
            return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
        },

        SearchImageWithPage:function(){
            var self = this;
            axios.post('dayu/image/pageList',Qs.stringify({categoryId:self.currentGroupId,pageSize:self.imagePageSize,pageNo:self.imageCurrentPage}))
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.imageList = response.data.list;
                        self.imageTotalCount = response.data.totalRecord;
                    } else {
                        self.$message("获取数据失败")
                    }
                })
                .catch(function (response) {
                    self.$message("获取数据失败")
                }
            );
        },

        editImage:function (row) {
            this.editImageForm.id = row.id;
            this.editImageForm.name = row.name;
            this.editImageForm.categoryId = row.categoryId;
            this.editImageFormVisible = true;
        },

        doEditImage:function () {
            var self = this;
            axios.post('dayu/image/edit',Qs.stringify(self.editImageForm))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.$message("编辑成功");
                            self.SearchImageWithPage();
                            self.editImageFormVisible = false;
                        } else {
                            self.$message("编辑失败");
                        }
                    })
                    .catch(function (response) {
                        self.$message("编辑失败");
                            }
                    );
        },

        doDeleteImage:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/image/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchImageWithPage();
                            self.$message('删除成功');
                        } else {
                            self.$message('删除失败');
                        }
                    })
                    .catch(function (response) {
                        self.$message('删除失败');
                    }
                );
            }).catch(() => {
            });
        },

        searchGroup:function(){
            var self = this;
            axios.post('dayu/imageGroup/list')
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.groupList = response.data.list;

                        } else {
                            self.$message("获取数据失败");
                        }
                    })
                    .catch(function (response) {
                                self.$message("获取数据失败");
                            }
                    );
        },



        handleImagePageSizeChange:function(size){
            var self = this;
            self.imagePageSize = size;
            self.SearchImageWithPage();
        },

        handleImagePageChange:function(currentPage){
            var self = this;
            self.imageCurrentPage = currentPage;
            self.SearchImageWithPage();
        },
        handleError(res) {
            this.$message("上传失败:"+res);
        },
        handleAvatarSuccess(res, file) {
            this.addImageForm.name = file.name;
            this.addImageForm.picture = 'http://image.dayuxiao.com/'+ res.key;
        },
        beforeAvatarUpload(file) {
            const isImg = false;
            if(file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif'){
                const isImg = true;
            }
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isImg) {
                this.$message.error('只能上传图片!');
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
            }
            return isImg && isLt2M;
        },
        getToken:function () {
            var self = this;
            axios.post('dayu/qiniu/token')
                    .then(function (response) {
                        self.postData.token = response.data;
                    })
                    .catch(function (response) {
                            }
                    );
        },
        doDelete:function(id){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/image/delete',Qs.stringify({id:id}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.SearchImageWithPage();
                                self.$message('删除成功');
                            } else {
                                self.$message('删除失败');
                            }
                        })
                        .catch(function (response) {
                                    self.$message('删除失败');
                                }
                        );
            }).catch(() => {
            });
        },

    },
    created:function() {
        var self = this;
        self.SearchImageWithPage();
        self.searchGroup();
        self.getToken();
    }
}
</script>

<style >
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 250px;
        text-align: center;
        text-align: -webkit-center;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 250px;
        height: 250px;
        line-height: 250px;
        text-align: center;
    }
    .avatar {
        width: auto;
        height: 250px;
        display: block;
    }

</style>



