<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>国家</el-col>
                <el-col style="width:56px">
                    <el-button @click="showAddCountryDialog()" :plain="true" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>
        <el-table :border="true" :data="countryList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="排序" width="70px" prop="orderNum">
                <template slot-scope="scope">
                    <el-input size="mini" style="text-align: center!important;" v-model="scope.row.orderNum" @change="order(scope.row.orderNum, scope.row.id)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="名称" prop="name"></el-table-column>
            <el-table-column label="操作" width="150px">
                <template slot-scope="scope">
                    <el-button @click="showEditCountryDialog(scope.$index,scope.row)" size="small" type="text">编辑</el-button>
                    <el-button @click="doDeleteCountry(scope.$index, scope.row)" size="small" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加国家" :visible.sync="addCountryFormVisible">
            <el-form :model="addCountryForm" :rules="rules" ref="addCountryFormRef" label-width="80px">
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="addCountryForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="addCountryFormVisible = false">取消</el-button>
                <el-button @click="doAddCountry">确定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑国家" :visible.sync="editCountryFormVisible">
            <el-form :model="editCountryForm" :rules="rules" ref="editCountryFormRef" label-width="80px">
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="editCountryForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="editCountryFormVisible = false">取消</el-button>
                <el-button @click="doEditCountry">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
export default {
    name:'null',
    data: function () {
        return {
            rules: {
                name: [
                    { required: true, message: '请填写名称', trigger: 'blur' }
                ]
            },
            addCountryForm:{},
            editCountryFormVisible:false,
            addCountryFormVisible:false,
            countryMultipleSelection:[],
            countryList:[],
            editCountryForm:{}
        }
    },
    methods:{
        order:function (value,id) {
            var self = this;
            axios.post('dayu/country/order',Qs.stringify({id:id,orderNum:value}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.searchCountry();
                        } else {

                        }
                    })
                    .catch(function (response) {

                            }
                    );
        },

        searchCountry:function(){
            var self = this;
            axios.post('dayu/country/list')
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.countryList = response.data.list;
                    } else {
                        console.log("获取数据失败");
                    }
                })
                .catch(function (response) {
                    console.log(response);
                }
            );
        },

        doDeleteCountry:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/country/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.searchCountry();
                        } else {
                            alert("删除失败");
                        }
                    })
                    .catch(function (response) {
                        console.log(response);
                    }
                );
            }).catch(() => {
            });
        },

        showAddCountryDialog:function(){
            var self = this;
            self.addCountryFormVisible = true
        },

        doAddCountry:function(){
            var self = this;
            this.$refs["addCountryFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/country/add',Qs.stringify(self.addCountryForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.addCountryFormVisible = false;
                                self.addCountryForm = {};
                                self.searchCountry();
                                self.$message("添加成功")
                            } else {
                                self.$message("添加失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        },

        showEditCountryDialog:function(index,row){
            var self = this;
            self.editCountryFormVisible = true;
            self.editCountryForm.name = row.name;
            self.editCountryForm.id = row.id;
        },

        doEditCountry:function(){
            var self = this;
            this.$refs["editCountryFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/country/edit',Qs.stringify(self.editCountryForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.editCountryFormVisible = false;
                                self.searchCountry();
                                self.$message("编辑成功")
                            } else {
                                self.$message("编辑失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        }
    },
    created:function() {
        var self = this;
        self.searchCountry();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



