<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>编辑幻灯片</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="sliderList" size="small" type="success">返回</el-button>
                </el-col>
            </el-row>
        </div>

        <el-form ref="addFormRef" :rules="rules" :model="addForm" label-width="80px">
            <el-form-item prop="positionId" label="位置">
                <el-select v-model.number="addForm.positionId" placeholder="请选择">
                    <el-option
                            v-for="position in positionList"
                            :key="position.id"
                            :label="position.name"
                            :value="position.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item prop="title" label="标题">
                <el-input v-model="addForm.title" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item prop="orderNum" label="排序">
                <el-input-number v-model="addForm.orderNum"></el-input-number>
            </el-form-item>
            <el-form-item prop="url" label="链接">
                <el-input v-model="addForm.url" placeholder="链接地址"></el-input>
            </el-form-item>
            <el-form-item prop="image" label="图片">
                <el-upload
                        class="slider-uploader"
                        action="http://up-z0.qiniu.com"
                        :show-file-list="false"
                        :on-error="handleError"
                        :on-success="handleAvatarSuccess"
                        :data="postData"
                        :before-upload="beforeAvatarUpload">
                    <img v-if="addForm.image" :src="addForm.image" class="slider">
                    <i v-else class="el-icon-plus slider-uploader-icon"></i>
                </el-upload>
            </el-form-item>
        </el-form>
        <div style="margin-top: 20px; text-align: right">
            <el-button @click="edit"  size="small" type="primary">编辑</el-button>
        </div>

    </div>
</template>
<script>
    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';


    export default {
        name:'null',
        data: function () {
            return {
                id:0,
                positionList:[],
                postData: {
                    token: ''
                },
                addForm:{
                    image:""
                },
                editorOption: {},
                rules: {
                    title: [
                        { required: true, message: '请填写标题', trigger: 'blur' }
                    ],
                    positionId: [
                        { required: true, message: '请填选择位置', trigger: 'blur' }
                    ],
                    url: [
                        { required: true, message: '请填写内容', trigger: 'blur'},
                    ],
                    image: [
                        { required: true, message: '请上传图片', trigger: 'blur' }
                    ],
                    orderNum :[
                        { required: true, message: '请填写序号', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{
            handleError(res) {
                this.$message("上传失败:"+res);
            },
            handleAvatarSuccess(res, file) {
                this.addForm.image = 'http://image.dayuxiao.com/'+ res.key;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },
            detail:function () {
                var self = this;
                axios.post('dayu/slider/detail',Qs.stringify({id:self.id}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.addForm = response.data.data;
                            } else {
                                self.$message('查询失败');
                            }
                        })
                        .catch(function (response) {
                                    self.$message('查询失败');
                                }
                        );
            },
            getToken:function () {
                var self = this;
                axios.post('dayu/qiniu/token')
                        .then(function (response) {
                            self.postData.token = response.data;
                        })
                        .catch(function (response) {
                                }
                        );
            },
            sliderList: function () {
                this.$router.push('/slider');
            },
            edit:function(){
                var self = this;
                this.$refs["addFormRef"].validate((valid) => {
                    if (valid) {
                        axios.post('dayu/slider/edit',Qs.stringify(self.addForm))
                                .then(function (response) {
                                    if (response.data.resultCode == 0) {
                                        self.$message('编辑成功');
                                        self.$router.push('/slider');
                                    } else {
                                        self.$message('编辑失败');
                                    }
                                })
                                .catch(function (response) {
                                            self.$message('编辑失败');
                                        }
                                );
                    } else {
                        self.$message('验证不通过');
                    }
                });
            },
            searchPosition:function(){
                var self = this;
                axios.post('dayu/sliderPosition/list')
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.positionList = response.data.list;

                            } else {
                                self.$message("获取数据失败");
                            }
                        })
                        .catch(function (response) {
                                    self.$message("获取数据失败");
                                }
                        );
            }
        },
        created:function() {
            var self = this;
            self.getToken();
            self.searchPosition();
            self.id = self.$route.params.id;
            self.detail();
        }
    }
</script>

<style >
    .slider-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 300px;
        text-align: center;
        text-align: -webkit-center;
    }
    .slider-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .slider-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 100%;
        height: 300px;
        line-height: 300px;
        text-align: center;
    }
    .slider {
        width: auto;
        height: 100%;
        display: block;
    }
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



