<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>幻灯片</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="addSlider" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>

        <div style="margin-bottom: 20px;">
            <el-select v-model.number="currentPositionId" placeholder="请选择">
                <el-option
                        v-for="position in positionList"
                        :key="position.id"
                        :label="position.name"
                        :value="position.id">
                </el-option>
            </el-select>
        </div>

        <el-table :border="true" :data="sliderList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="排序" width="70px" prop="orderNum">
                <template slot-scope="scope">
                    <el-input size="mini" style="text-align: center!important;" v-model="scope.row.orderNum" @change="order(scope.row.orderNum, scope.row.id)"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="标题" prop="title"></el-table-column>
            <el-table-column label="创建时间" :formatter="dateFormatter"  width="170px" prop="createTime"></el-table-column>
            <el-table-column label="操作" width="150px">
                <template slot-scope="scope">
                    <el-button size="small" @click="editSlider(scope.row.id)" type="text">编辑</el-button>
                    <el-button size="small"  @click="doDeleteSlider(scope.$index, scope.row)" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';

export default {
    name:'null',
    data: function () {
        return {
            sliderList:[],
            positionList:[],
            currentPositionId:null
        }
    },
    watch:{
        currentPositionId:function (val,oldVal) {
            this.SearchSlider();
        }
    },
    methods:{
        order:function (value,id) {
            var self = this;
            axios.post('dayu/slider/order',Qs.stringify({id:id,orderNum:value}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchSlider();
                        } else {

                        }
                    })
                    .catch(function (response) {

                            }
                    );
        },
        addSlider: function () {
            this.$router.push('/addSlider');
        },

        editSlider: function (id) {
            this.$router.push('/editSlider/'+id);
        },

        dateFormatter: function (row, column,cellValue) {
            return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
        },

        SearchSlider:function(){
            var self = this;
            axios.post('dayu/slider/list',Qs.stringify({positionId:self.currentPositionId}))
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.sliderList = response.data.list;
                    } else {
                        self.$message("获取数据失败")
                    }
                })
                .catch(function (response) {
                    self.$message("获取数据失败")
                }
            );
        },

        doDeleteSlider:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/slider/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchSlider();
                            self.$message('删除成功');
                        } else {
                            self.$message('删除失败');
                        }
                    })
                    .catch(function (response) {
                        self.$message('删除失败');
                    }
                );
            }).catch(() => {
            });
        },
        searchPosition:function(){
            var self = this;
            axios.post('dayu/sliderPosition/list')
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.positionList = response.data.list;

                        } else {
                            self.$message("获取数据失败");
                        }
                    })
                    .catch(function (response) {
                                self.$message("获取数据失败");
                            }
                    );
        },

    },
    created:function() {
        var self = this;
        self.searchPosition();
        self.SearchSlider();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .el-pagination{padding: 10px;background-color: #eef1f6;border: 1px solid #e6ebf5;border-top: 0px;}
</style>



