<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>添加移民</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="immigrationList" size="small" type="success">返回</el-button>
                </el-col>
            </el-row>
        </div>

        <el-form ref="addFormRef" :rules="rules" :model="addForm" >
            <el-form-item prop="title">
                <el-input v-model="addForm.title" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item prop="country">
                <el-input v-model="addForm.country" placeholder="国家"></el-input>
            </el-form-item>
            <el-form-item prop="subTitle">
                <el-input v-model="addForm.subTitle" placeholder="副标题"></el-input>
            </el-form-item>
            <el-form-item prop="description">
                <el-input type="textarea" v-model="addForm.description" placeholder="简介"></el-input>
            </el-form-item>
            <el-form-item prop="picture">
                <el-upload
                        class="avatar-uploader"
                        action="http://up-z0.qiniu.com"
                        :show-file-list="false"
                        :on-error="handleError"
                        :on-success="handleAvatarSuccess"
                        :data="postData"
                        :before-upload="beforeAvatarUpload">
                    <img v-if="addForm.picture" :src="addForm.picture" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>

            <el-card class="box-card" style="margin-bottom: 20px;">

            <el-row>
                <el-col :span="4" >
                    <el-form-item label="投资额度" prop="price">
                        <el-input v-model="addForm.price"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4" :offset="1">
                    <el-form-item label="办理周期" prop="period">
                        <el-input v-model="addForm.period"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4" :offset="1">
                    <el-form-item label="身份类型" prop="identityType">
                        <el-input v-model="addForm.identityType"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4" :offset="1">
                    <el-form-item label="居住要求" prop="livingRequirement">
                        <el-input v-model="addForm.livingRequirement"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4" :offset="1">
                    <el-form-item label="犯罪记录" prop="criminalRecord">
                        <el-input v-model="addForm.criminalRecord"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

                </el-card>

            <el-tabs type="border-card" style="margin-bottom: 20px;">
                <el-tab-pane label="推荐房产">

                    <div style="margin-bottom: 20px;">
                        <el-select
                                style="width: 500px"
                                v-model="selectedHouse"
                                multiple
                                filterable
                                remote
                                reserve-keyword
                                placeholder="请输入关键词"
                                :remote-method="queryHouse"
                                :loading="loading">
                            <el-option
                                    v-for="house in optionHouse"
                                    :key="house.id"
                                    :label="house.title"
                                    :value="house.id">
                                <span style="float: left">{{ house.title }}</span>
                                <span style="float: right; color: #8492a6; font-size: 13px">{{ house.country }}</span>
                            </el-option>
                        </el-select>
                        <el-button @click="addRelatedHouse" >添加</el-button>
                    </div>

                    <el-table
                            border
                            :data="addForm.relatedHouse"
                            style="width: 100%">
                        <el-table-column
                                prop="id"
                                label="ID">
                        </el-table-column>
                        <el-table-column
                                prop="title"
                                label="姓名"
                                width="标题">
                        </el-table-column>
                        <el-table-column
                                prop="country"
                                label="国家">
                        </el-table-column>
                        <el-table-column
                                label="操作">
                            <template slot-scope="scope">
                                <el-button size="small" @click="deleteRelatedHouse(scope.$index)" type="text">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="实地考察">

                    <div style="margin-bottom: 20px;">
                        <el-select
                                style="width: 500px"
                                v-model="selectedVideo"
                                multiple
                                filterable
                                remote
                                reserve-keyword
                                placeholder="请输入关键词"
                                :remote-method="queryVideo"
                                :loading="loadingVideo">
                            <el-option
                                    v-for="video in optionVideo"
                                    :key="video.id"
                                    :label="video.name"
                                    :value="video.id">
                            </el-option>
                        </el-select>
                        <el-button @click="addRelatedVideo" >添加</el-button>
                    </div>

                    <el-table
                            border
                            :data="addForm.videoList"
                            style="width: 100%">
                        <el-table-column
                                prop="id"
                                label="ID">
                        </el-table-column>
                        <el-table-column
                                prop="name"
                                label="名称">
                        </el-table-column>
                        <el-table-column
                                label="操作">
                            <template slot-scope="scope">
                                <el-button size="small" @click="deleteRelatedVideo(scope.$index)" type="text">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>

                </el-tab-pane>
            </el-tabs>

            <el-tabs type="border-card">
                <el-tab-pane label="项目优势">
                    <el-form-item prop="projectBenefits">
                        <editor v-model="addForm.projectBenefits"></editor>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="申请条件">
                    <el-form-item prop="applicationCondition">
                        <editor v-model="addForm.applicationCondition"></editor>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="申请流程">
                    <el-form-item prop="applicationProcess">
                        <editor v-model="addForm.applicationProcess"></editor>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="费用详情">
                    <el-form-item prop="priceDetail">
                        <editor v-model="addForm.priceDetail"></editor>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="成功案例">
                    <el-form-item prop="successCase">
                        <editor v-model="addForm.successCase"></editor>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="国家介绍">
                    <el-form-item prop="countryInstroduction">
                        <editor v-model="addForm.countryInstroduction"></editor>
                    </el-form-item>
                </el-tab-pane>
            </el-tabs>

        </el-form>
        <div style="margin-top: 20px; text-align: right">
            <el-button @click="add"  size="small" type="primary">添加</el-button>
        </div>

    </div>
</template>
<script>
    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';


    export default {
        name:'null',
        data: function () {
            return {
                selectedHouse:[],
                optionHouse:[],
                loading: false,
                selectedVideo:[],
                optionVideo:[],
                loadingVideo: false,

                postData: {
                    token: ''
                },
                addForm:{
                    picture:"",
                    relatedHouse:[],
                    videoList:[],
                },
                editorOption: {},
                rules: {
                    title: [
                        { required: true, message: '请填写标题', trigger: 'blur' }
                    ],
                    price: [
                        { required: true, message: '请输入投资额度', trigger: 'blur'}
                    ],
                    picture: [
                        { required: true, message: '请上传图片', trigger: 'blur' }
                    ],
                    period: [
                        { required: true, message: '请填写办理周期', trigger: 'blur' }
                    ],
                    identityType: [
                        { required: true, message: '请填写身份类型',  trigger: 'blur' }
                    ],
                    livingRequirement: [
                        { required: true, message: '请填写居住要求', trigger: 'blur' }
                    ],
                    criminalRecord: [
                        { required: true, message: '请填写犯罪记录', trigger: 'blur' }
                    ],
                    projectBenefits: [
                        { required: true, message: '请填写项目优势', trigger: 'blur' }
                    ],
                    applicationCondition: [
                        { required: true, message: '请填写申请条件', trigger: 'blur' }
                    ],
                    applicationProcess: [
                        { required: true, message: '请填写申请流程', trigger: 'blur' }
                    ],
                    priceDetail: [
                        { required: true, message: '请填写价格详情', trigger: 'blur' }
                    ],
                    successCase: [
                        { required: true, message: '请填写成功案例', trigger: 'blur' }
                    ],
                    countryInstroduction: [
                        { required: true, message: '请填写国家介绍', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{

            queryHouse(query) {
                var self = this;
                if (query !== '' && query.length>=2) {
                    self.loading = true;
                    axios.post('dayu/house/query?query='+query)
                            .then(function (response) {
                                self.loading = false;
                                self.optionHouse = response.data.list;
                            })
                            .catch(function (response) {
                                        self.loading = false;
                                        self.optionHouse = [];
                                    }
                            );
                } else {
                    self.optionHouse = [];
                }
            },

            addRelatedHouse:function () {
                var self = this;
                for (var i=0;i<self.selectedHouse.length;i++){
                    axios.post('dayu/house/detail?id='+self.selectedHouse[i])
                            .then(function (response) {
                                var house = {id:response.data.data.id,title:response.data.data.title,country:response.data.data.country};
                                self.addForm.relatedHouse.push(house);
                            })
                            .catch(function (response) {
                                    }
                            );
                }
                self.selectedHouse = [];
            },

            deleteRelatedHouse:function (index) {
                this.addForm.relatedHouse.splice(index,1);
            },


            queryVideo(query) {
                var self = this;
                if (query !== '' && query.length>=2) {
                    self.loadingVideo = true;
                    axios.post('dayu/video/query?query='+query)
                            .then(function (response) {
                                self.loadingVideo = false;
                                self.optionVideo = response.data.list;
                            })
                            .catch(function (response) {
                                        self.loadingVideo = false;
                                        self.optionVideo = [];
                                    }
                            );
                } else {
                    self.optionVideo = [];
                }
            },

            addRelatedVideo:function () {
                var self = this;
                for (var i=0;i<self.selectedVideo.length;i++){
                    axios.post('dayu/video/detail?id='+self.selectedVideo[i])
                            .then(function (response) {
                                var video = {id:response.data.data.id,name:response.data.data.name};
                                self.addForm.videoList.push(video);
                            })
                            .catch(function (response) {
                                    }
                            );
                }
                self.selectedVideo = [];
            },

            deleteRelatedVideo:function (index) {
                this.addForm.videoList.splice(index,1);
            },


            handleError(res) {
                this.$message("上传失败:"+res);
            },
            handleAvatarSuccess(res, file) {
                this.addForm.picture = 'http://image.dayuxiao.com/'+ res.key;
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isJPG) {
                    this.$message.error('上传图片只能是 JPG 格式!');
                }
                if (!isLt2M) {
                    this.$message.error('上传图片大小不能超过 2MB!');
                }
                return isJPG && isLt2M;
            },

            getToken:function () {
                var self = this;
                axios.post('dayu/qiniu/token')
                        .then(function (response) {
                            self.postData.token = response.data;
                        })
                        .catch(function (response) {
                                }
                        );
            },

            immigrationList: function () {
                this.$router.push('/immigration');
            },
            add:function(){
                var self = this;
                this.$refs["addFormRef"].validate((valid) => {
                    if (valid) {
                        self.addForm.videos = JSON.stringify(self.addForm.videoList);
                        self.addForm.houses = JSON.stringify(self.addForm.relatedHouse);
                        axios.post('dayu/immigration/add',Qs.stringify(self.addForm))
                                .then(function (response) {
                                    if (response.data.resultCode == 0) {
                                        self.$message('添加成功');
                                        self.$router.push('/immigration');
                                    } else {
                                        self.$message('添加失败');
                                    }
                                })
                                .catch(function (response) {
                                            self.$message('添加失败');
                                        }
                                );
                    } else {
                        self.$message('验证不通过');
                    }
                });
            },
        },
        created:function() {
            var self = this;
            self.getToken();
        }
    }
</script>

<style>
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 250px;
        text-align: center;
        text-align: -webkit-center;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 250px;
        height: 250px;
        line-height: 250px;
        text-align: center;
    }
    .avatar {
        width: auto;
        height: 250px;
        display: block;
    }
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



