<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>{{editForm.name}}</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="categoryList" size="small" type="success">返回</el-button>
                </el-col>
            </el-row>
        </div>

        <el-form ref="editFormRef" :rules="rules" :model="editForm">
            <el-form-item prop="content">
                <editor v-model="editForm.content"></editor>
            </el-form-item>
        </el-form>
        <div style="margin-top: 20px; text-align: right">
            <el-button @click="edit"  size="small" type="primary">编辑</el-button>
        </div>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';


export default {
    name:'null',
    data: function () {
        return {
            editForm:{
                id:0,
                name:"",
                content:""
            },
            id:0,
            rules: {
                content: [
                    { required: true, message: '请填写内容', trigger: 'blur'},
                ]
            }
        }
    },
    methods:{
        categoryList: function () {
            this.$router.push('/category');
        },
        detail:function () {
            var self = this;
            axios.post('dayu/category/detail',Qs.stringify({id:self.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.editForm.id = response.data.data.id;
                            self.editForm.name = response.data.data.name;
                            self.editForm.content = response.data.data.content;
                        } else {
                            self.$message('查询失败');
                        }
                    })
                    .catch(function (response) {
                                self.$message('查询失败');
                            }
                    );
        },
        edit:function(){
            var self = this;
            this.$refs["editFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/category/edit',Qs.stringify(self.editForm))
                            .then(function (response) {
                                if (response.data.resultCode == 0) {
                                    self.$message('编辑成功');
                                } else {
                                    self.$message('编辑失败');
                                }
                            })
                            .catch(function (response) {
                                self.$message('编辑失败');
                                    }
                            );
                } else {
                    self.$message('验证不通过');
                }
            });
        },
    },
    created:function() {
        var self = this;
        self.id = self.$route.params.id;
        self.detail();
    }
}
</script>

<style >
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



