<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>视频</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="addVideo" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>

        <div style="display: flex;flex-wrap:wrap;">
            <el-card
                    v-for="video in videoList" :key="video.id"
                    :body-style="{ padding: '0px' }"
                    style="text-align: center;width: 300px;margin-bottom: 10px;margin-right: 10px;">

                <video-player  class="video"
                               style="height: 200px;overflow: hidden"
                               ref="videoPlayer"
                               :playsinline="true"
                               :options="video.option">
                </video-player>

                <div style="padding: 5px;background-color:#eef1f6 ;text-align: left">
                    <span style="font-size: 12px;height: 12px;line-height: 12px;width:200px; overflow: hidden;display: inline-block">{{video.name}}</span>

                    <i @click="doDeleteVideo(video.id)" style="cursor: pointer;float: right;color: red; margin-left: 10px;margin-top: 3px" class="el-icon-circle-close"></i>
                    <i
                            @click="editVideo(video.id)"
                            style="cursor: pointer;float: right;margin-left: 10px;margin-top: 3px"
                            class="el-icon-edit"
                    ></i>

                </div>
            </el-card>
        </div>

        <el-pagination @current-change="handleVideoPageChange" :current-page="videoCurrentPage" :page-size="videoPageSize" @size-change="handleVideoPageSizeChange" :total="videoTotalCount"></el-pagination>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';

export default {
    name:'null',
    data: function () {
        return {
            videoPageSize:20,
            videoCurrentPage:0,
            videoTotalCount:0,
            videoList:[]
        }
    },
    methods:{
        addVideo: function () {
            this.$router.push('/addVideo');
        },

        editVideo: function (id) {
            this.$router.push('/editVideo/'+id);
        },

        dateFormatter: function (row, column,cellValue) {
            return moment(cellValue).format("YYYY-MM-DD HH:mm:ss");
        },

        SearchVideoWithPage:function(){
            var self = this;
            axios.post('dayu/video/pageList',Qs.stringify({pageSize:self.videoPageSize,pageNo:self.videoCurrentPage}))
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.videoList = response.data.list;
                        for (var i=0;i<self.videoList.length;i++){
                            self.videoList[i].option = {
                                height: '200',
                                width:'300',
                                sources: [{
                                    type: "video/mp4",
                                    src: self.videoList[i].path
                                }]
                            }
                        }
                        self.videoTotalCount = response.data.totalRecord;
                    } else {
                        self.$message("获取数据失败")
                    }
                })
                .catch(function (response) {
                    self.$message("获取数据失败")
                }
            );
        },

        doDeleteVideo:function(id){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/video/delete',Qs.stringify({id:id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.SearchVideoWithPage();
                            self.$message('删除成功');
                        } else {
                            self.$message('删除失败');
                        }
                    })
                    .catch(function (response) {
                        self.$message('删除失败');
                    }
                );
            }).catch(() => {
            });
        },



        handleVideoPageSizeChange:function(size){
            var self = this;
            self.videoPageSize = size;
            self.SearchVideoWithPage();
        },

        handleVideoPageChange:function(currentPage){
            var self = this;
            self.videoCurrentPage = currentPage;
            self.SearchVideoWithPage();
        }

    },
    created:function() {
        var self = this;
        self.SearchVideoWithPage();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
    .el-pagination{padding: 10px;background-color: #eef1f6;border: 1px solid #e6ebf5;border-top: 0px;}
</style>



