import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Category from '@/components/category/category'
import New from '@/components/new/new'
import AddNew from '@/components/new/addNew'
import EditNew from '@/components/new/editNew'
import House from '@/components/house/house'
import AddHouse from '@/components/house/addHouse'
import EditHouse from '@/components/house/editHouse'
import Immigration from '@/components/immigration/immigration'
import AddImmigration from '@/components/immigration/addImmigration'
import EditImmigration from '@/components/immigration/editImmigration'
import Passport from '@/components/passport/passport'
import AddPassport from '@/components/passport/addPassport'
import EditPassport from '@/components/passport/editPassport'
import Advisory from '@/components/advisory/advisory'
import SliderPosition from '@/components/slider/sliderPosition'
import Video from '@/components/video/video'
import Image from '@/components/image/image'
import AddVideo from '@/components/video/addVideo'
import EditVideo from '@/components/video/editVideo'
import Slider from '@/components/slider/slider'
import AddSlider from '@/components/slider/addSlider'
import EditSlider from '@/components/slider/editSlider'
import Country    from '@/components/country/country'
import Recommend  from '@/components/recommend/recommend'
import RecommendPosition from '@/components/recommend/recommendPosition'
import SinglePageEdit from '@/components/category/singlePage'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/house',
            name: 'House',
            component: House
        },
        {
            path: '/addHouse',
            name: 'AddHouse',
            component: AddHouse
        },
        {
            path: '/editHouse/:id',
            name: 'EditHouse',
            component: EditHouse
        },
        {
            path: '/immigration',
            name: 'Immigration',
            component: Immigration
        },
        {
            path: '/addImmigration',
            name: 'AddImmigration',
            component: AddImmigration
        },
        {
            path: '/editImmigration/:id',
            name: 'EditImmigration',
            component: EditImmigration
        },
        {
            path: '/pageEdit/:id',
            name: 'SinglePageEdit',
            component: SinglePageEdit
        },
        {
            path: '/passport',
            name: 'Passport',
            component: Passport
        },
        {
            path: '/addPassport',
            name: 'AddPassport',
            component: AddPassport
        },
        {
            path: '/editPassport/:id',
            name: 'EditPassport',
            component: EditPassport
        },
        {
            path: '/category',
            name: 'Category',
            component: Category
        },
        {
            path: '/new',
            name: 'New',
            component: New
        },
        {
            path: '/addNew',
            name: 'AddNew',
            component: AddNew
        },
        {
            path: '/editNew/:id',
            name: 'EditNew',
            component: EditNew
        },
        {
            path: '/',
            name: 'HelloWorld',
            component: HelloWorld
        },
        {
            path: '/advisory',
            name: 'Advisory',
            component: Advisory
        },
        {
            path: '/sliderPosition',
            name: 'SliderPosition',
            component: SliderPosition
        },
        {
            path: '/video',
            name: 'Video',
            component: Video
        },
        {
            path: '/image',
            name: 'Image',
            component: Image
        },
        {
            path: '/addVideo',
            name: 'AddVideo',
            component: AddVideo
        },
        {
            path: '/editVideo/:id',
            name: 'EditVideo',
            component: EditVideo
        },
        {
            path: '/slider',
            name: 'Slider',
            component: Slider
        },
        {
            path: '/addSlider',
            name: 'AddSlider',
            component: AddSlider
        },
        {
            path: '/editSlider/:id',
            name: 'EditSlider',
            component: EditSlider
        },
        {
            path: '/country',
            name: 'Country',
            component: Country
        },
        {
            path: '/recommendPosition',
            name: 'RecommendPosition',
            component: RecommendPosition
        },
        {
            path: '/recommend',
            name: 'Recommend',
            component: Recommend
        }
    ]
})



