<template>
    <div>
        <el-dialog title="选择图片" width="70%" :visible.sync="imageResourceDialogVisible" append-to-body>
            <image-resource @select="insertImage"></image-resource>
        </el-dialog>

        <el-tabs v-model="activeName" type="border-card">
            <el-tab-pane name="first" label="视频列表">
                <div style="display: flex;flex-wrap:wrap;" >
                    <el-card
                            v-for="video in videoList" :key="video.id"
                            :body-style="{ padding: '0px' }"
                            style="text-align: center;width: 300px;margin-bottom: 10px;margin-right: 10px;">

                        <video-player  class="video"
                                       style="height: 200px;overflow: hidden"
                                       ref="videoPlayer"
                                       :playsinline="true"
                                       :options="video.option">
                        </video-player>
                        <div @click="selectVideo(video)" style="padding: 5px;background-color:#eef1f6 ;text-align: left;cursor: pointer">
                            <span style="font-size: 12px;height: 12px;line-height: 12px;width:200px; overflow: hidden;display: inline-block">{{video.name}}</span>

                            <i  style="cursor: pointer;float: right;margin-left: 10px;margin-top: 3px" class="el-icon-success"></i>
                        </div>
                    </el-card>
                </div>
                <el-pagination @current-change="handleVideoPageChange" :current-page="videoCurrentPage" :page-size="videoPageSize" @size-change="handleVideoPageSizeChange" :total="videoTotalCount"></el-pagination>
            </el-tab-pane>
            <el-tab-pane name="second" label="添加视频">
                <el-form ref="addFormRef" :rules="rules" :model="addForm">
                    <el-form-item prop="name">
                        <el-input v-model="addForm.name" placeholder="标题"></el-input>
                    </el-form-item>
                    <el-form-item prop="image" style="margin-bottom: 20px;">
                        <div @click="showImageDialog" style="height: 250px;width: 500px; cursor: pointer;border: 1px dashed #d9d9d9; border-radius: 6px;">
                            <p v-if="!addForm.image" style="text-align: center;line-height: 250px;">选择视频封面图片</p>
                            <img  v-if="addForm.image" :src="addForm.image" style="width: 100%;height: 100%;" >
                        </div>
                    </el-form-item>
                    <el-form-item prop="path">
                        <el-upload
                                class="video-uploader"
                                action="http://up-z0.qiniu.com"
                                :show-file-list="false"
                                :on-error="handleError"
                                :on-success="handleAvatarSuccess"
                                :on-progress="handProcess"
                                :data="postData"
                                :before-upload="beforeAvatarUpload">

                            <video-player  v-if="addForm.path && !isUpload" class="video"
                                           ref="videoPlayer"
                                           :playsinline="true"
                                           :options="playerOptions">
                            </video-player>
                            <i v-if="!addForm.path && !isUpload" class="el-icon-plus video-uploader-icon"></i>
                            <el-progress v-if="isUpload && percent>0 && percent<100" type="circle" style="margin-top: 87px;" :percentage="percent"></el-progress>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <div style="margin-top: 20px; text-align: right">
                    <el-button @click="add"  size="small" type="primary">添加</el-button>
                </div>
            </el-tab-pane>
        </el-tabs>

    </div>
</template>

<script>

    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';

    export default {
        data: function () {
            return {
                activeName:"first",
                videoPageSize:20,
                videoCurrentPage:0,
                videoTotalCount:0,
                videoList:[],
                imageResourceDialogVisible:false,
                playerOptions: {
                    muted: false,
                    language: 'en',
                    playbackRates: [0.7, 1.0, 1.5, 2.0],
                    sources: [{
                        type: "video/mp4",
                        src: ""
                    }]
                },
                isUpload:false,
                postData: {
                    token: ''
                },
                percent:0,
                addForm:{
                    path:""
                },
                rules: {
                    name: [
                        { required: true, message: '请填写标题', trigger: 'blur' }
                    ],
                    path: [
                        { required: true, message: '请上传视频', trigger: 'blur' }
                    ],
                    image: [
                        { required: true, message: '请上传封面照片', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{

            selectVideo:function (url) {
                this.$emit("select",url)
            },

            SearchVideoWithPage:function(){
                var self = this;
                axios.post('dayu/video/pageList',Qs.stringify({pageSize:self.videoPageSize,pageNo:self.videoCurrentPage}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.videoList = response.data.list;
                                for (var i=0;i<self.videoList.length;i++){
                                    self.videoList[i].option = {
                                        height: '200',
                                        width:'300',
                                        sources: [{
                                            type: "video/mp4",
                                            src: self.videoList[i].path
                                        }]
                                    }
                                }
                                self.videoTotalCount = response.data.totalRecord;
                            } else {
                                self.$message("获取数据失败")
                            }
                        })
                        .catch(function (response) {
                                    self.$message("获取数据失败")
                                }
                        );
            },
            handleVideoPageSizeChange:function(size){
                var self = this;
                self.videoPageSize = size;
                self.SearchVideoWithPage();
            },

            handleVideoPageChange:function(currentPage){
                var self = this;
                self.videoCurrentPage = currentPage;
                self.SearchVideoWithPage();
            },
            insertImage:function (value) {
                this.addForm.image = value;
                this.imageResourceDialogVisible = false;
            },
            showImageDialog:function(state){
                this.imageResourceDialogVisible = true;
            },
            handProcess(event, file, fileList){
                this.isUpload = true;
                this.percent = Math.floor(event.percent);
            },
            handleError(res) {
                this.$message("上传失败:"+res);
                this.percent = 0;
                this.isUpload = false;
            },
            handleAvatarSuccess(res, file) {
                this.percent = 100;
                this.addForm.path = 'http://image.dayuxiao.com/'+ res.key;
                this.playerOptions.sources[0].src = this.addForm.path;
                this.isUpload = false;
            },
            beforeAvatarUpload(file) {
                const isMp4 = file.type === 'video/mp4';
                const isLt200M = file.size / 1024 / 1024 < 200;

                if (!isMp4) {
                    this.$message.error('上传图片只能是MP4格式!');
                }
                if (!isLt200M) {
                    this.$message.error('上传视频大小不能超过 200MB!');
                }
                return isMp4 && isLt200M;
            },
            getToken:function () {
                var self = this;
                axios.post('dayu/qiniu/token')
                        .then(function (response) {
                            self.postData.token = response.data;
                        })
                        .catch(function (response) {
                                }
                        );
            },

            add:function(){
                var self = this;
                this.$refs["addFormRef"].validate((valid) => {
                    if (valid) {
                        axios.post('dayu/video/add',Qs.stringify(self.addForm))
                                .then(function (response) {
                                    if (response.data.resultCode == 0) {
                                        self.$message('添加成功');
                                        self.SearchVideoWithPage();
                                        self.addForm = {};
                                        self.activeName = "first";
                                    } else {
                                        self.$message('添加失败');
                                    }
                                })
                                .catch(function (response) {
                                            self.$message('添加失败');
                                        }
                                );
                    } else {
                        self.$message('验证不通过');
                    }
                });
            }
        },
        created:function() {
            var self = this;
            self.SearchVideoWithPage();
            self.getToken();
        }
    }

</script>

<style>
    .video-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 300px;
    }
    .video-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .video-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 100%;
        height: 300px;
        line-height: 300px;
        text-align: center;
    }
    .video {
        width: 100%;
        text-align: center;
        text-align: -webkit-center;
    }
</style>


