<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>位置</el-col>
                <el-col style="width:56px">
                    <el-button @click="showAddPositionDialog()" :plain="true" size="small" type="success">添加</el-button>
                </el-col>
            </el-row>
        </div>
        <el-table :border="true" :data="positionList" :stripe="true">
            <el-table-column label="ID" prop="id" width="50px"></el-table-column>
            <el-table-column label="名称" prop="name"></el-table-column>
            <el-table-column label="类型" prop="type" width="150px"></el-table-column>
            <el-table-column label="操作" width="150px">
                <template slot-scope="scope">
                    <el-button @click="showEditPositionDialog(scope.$index,scope.row)" size="small" type="text">编辑</el-button>
                    <el-button @click="doDeletePosition(scope.$index, scope.row)" size="small" type="text">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加位置" :visible.sync="addPositionFormVisible">
            <el-form :model="addPositionForm" :rules="rules" ref="addPositionFormRef" label-width="80px">
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="addPositionForm.name"></el-input>
                </el-form-item>
                <el-form-item label="类别" prop="type">
                    <el-select v-model="addPositionForm.type" placeholder="类型">
                        <el-option label="资讯" value="new"></el-option>
                        <el-option label="房产" value="house"></el-option>
                        <el-option label="移民" value="immigration"></el-option>
                        <el-option label="护照" value="passport"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="addPositionFormVisible = false">取消</el-button>
                <el-button @click="doAddPosition">确定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑位置" :visible.sync="editPositionFormVisible">
            <el-form :model="editPositionForm" :rules="rules" ref="editPositionFormRef" label-width="80px">
                <el-form-item label="名称" prop="name">
                    <el-input :maxlength="500" :minlength="0" placeholder="名称" v-model="editPositionForm.name"></el-input>
                </el-form-item>
                <el-form-item label="类别" prop="type">
                    <el-select v-model="editPositionForm.type" placeholder="类型">
                        <el-option label="资讯" value="new"></el-option>
                        <el-option label="房产" value="house"></el-option>
                        <el-option label="移民" value="immigration"></el-option>
                        <el-option label="护照" value="passport"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="dialog-footer" slot="footer">
                <el-button @click="editPositionFormVisible = false">取消</el-button>
                <el-button @click="doEditPosition">确定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
export default {
    name:'null',
    data: function () {
        return {
            rules: {
                name: [
                    { required: true, message: '请填写名称', trigger: 'blur' }
                ],
                type: [
                    { required: true, message: '请选择', trigger: 'change' }
                ]
            },
            addPositionForm:{},
            editPositionFormVisible:false,
            addPositionFormVisible:false,
            positionMultipleSelection:[],
            positionList:[],
            editPositionForm:{}
        }
    },
    methods:{

        searchPosition:function(){
            var self = this;
            axios.post('dayu/recommendPosition/list')
                .then(function (response) {
                    if (response.data.resultCode == 0) {
                        self.positionList = response.data.list;

                    } else {
                        self.$message("获取数据失败");
                    }
                })
                .catch(function (response) {
                    self.$message("获取数据失败");
                }
            );
        },

        doDeletePosition:function(index,row){
            var self = this;
            this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning',
                center: true
            }).then(() => {
                axios.post('dayu/recommendPosition/delete',Qs.stringify({id:row.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.searchPosition();
                            self.$message("删除成功");
                        } else {
                            self.$message("删除失败");
                        }
                    })
                    .catch(function (response) {
                        self.$message("删除失败");
                    }
                );
            }).catch(() => {
            });
        },

        showAddPositionDialog:function(){
            var self = this;
            self.addPositionFormVisible = true
        },

        doAddPosition:function(){
            var self = this;
            this.$refs["addPositionFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/recommendPosition/add',Qs.stringify(self.addPositionForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.addPositionFormVisible = false;
                                self.addPositionForm = {};
                                self.searchPosition();
                                self.$message("添加成功")
                            } else {
                                self.$message("添加失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        },

        showEditPositionDialog:function(index,row){
            var self = this;
            self.editPositionFormVisible = true;
            self.editPositionForm.name = row.name;
            self.editPositionForm.type = row.type;
            self.editPositionForm.id = row.id;
        },

        doEditPosition:function(){
            var self = this;
            this.$refs["editPositionFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/recommendPosition/edit',Qs.stringify(self.editPositionForm))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.editPositionFormVisible = false;
                                self.searchPosition();
                                self.$message("编辑成功")
                            } else {
                                self.$message("编辑失败")
                            }
                        })
                        .catch(function (response) {
                            console.log(response);
                        }
                    );
                } else {
                    self.$message("校验失败")
                }
            });
        }
    },
    created:function() {
        var self = this;
        self.searchPosition();
    }
}
</script>

<style>
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



