<template>
    <div>

        <el-dialog title="选择图片" width="70%" :visible.sync="imageResourceDialogVisible">
            <image-resource @select="insertImage"></image-resource>
        </el-dialog>

        <el-dialog title="选择视频" width="70%" :visible.sync="videoResourceDialogVisible">
            <video-resource @select="insertVideo"></video-resource>
        </el-dialog>

        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>添加房产</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="houseList" size="small" type="success">返回</el-button>
                </el-col>
            </el-row>
        </div>

        <el-form ref="addFormRef" :rules="rules" :model="addForm" >
            <el-form-item prop="title">
                <el-input v-model="addForm.title" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item prop="price">
                <el-input placeholder="价格" v-model.number="addForm.price" class="input-with-select">
                    <el-select style="width: 100px" v-model="addForm.priceUnit" slot="append" placeholder="请选择">
                        <el-option label="万欧元" value="欧元"></el-option>
                        <el-option label="万美元" value="美元"></el-option>
                        <el-option label="万人民币" value="人民币"></el-option>
                    </el-select>
                </el-input>
            </el-form-item>
            <el-form-item prop="description">
                <el-input type="textarea" v-model="addForm.description" placeholder="简介"></el-input>
            </el-form-item>

            <el-tabs type="border-card" style="margin-bottom: 20px;">
                <el-tab-pane label="房产相册">
                    <el-form-item prop="picture">
                        <div class="picture-item" v-for="(pic,index) in addForm.pictureList">
                            <p class="picture-item-close" @click="deleteImage(index)"><i style="font-size: 30px;" class="el-icon-error"></i></p>
                            <img :src="pic">
                        </div>
                        <div class="picture-item" @click="addImage">
                            + 添加照片
                        </div>
                    </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="房产视频">
                    <el-form-item prop="videos">
                        <div class="picture-item" style="height: 170px;" v-for="(video,index) in addForm.videoList">
                            <el-card
                                    :body-style="{ padding: '0px' }"
                                    style="text-align: center;width: 198px;margin-bottom: 10px;margin-right: 10px;">
                                <video-player  class="video"
                                               style="height: 148px;overflow: hidden"
                                               ref="videoPlayer"
                                               :playsinline="true"
                                               :options="video.option">
                                </video-player>
                                <div style="cursor: pointer;height: 20px;line-height: 20px;background-color: red;" @click="deleteVideo(index)"><i class="el-icon-error"></i></div>
                            </el-card>
                        </div>
                        <div class="picture-item" style="height: 170px;" @click="addVideo">
                            + 添加视频
                        </div>
                    </el-form-item>
                </el-tab-pane>
            </el-tabs>

            <el-card class="box-card" style="margin-bottom: 20px;">

            <el-row>
                <el-col :span="5">
                    <el-form-item   label="国家" prop="country">
                        <br>
                        <el-select  v-model.number="addForm.country" placeholder="请选择">
                            <el-option
                                    v-for="country in countryList"
                                    :key="country.id"
                                    :label="country.name"
                                    :value="country.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1" >
                    <el-form-item  label="面积" prop="area">
                        <br>
                        <el-input  v-model="addForm.area">
                            <template slot="append">平米</template>
                        </el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1" >
                    <el-form-item  label="位置" prop="region">
                        <br>
                        <el-input  v-model="addForm.region"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1">
                    <el-form-item  label="楼层" prop="floor">
                        <br>
                        <el-input-number  v-model="addForm.floor"></el-input-number>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="5">
                    <el-form-item label="物业" prop="houseType">
                        <br>
                        <el-select  v-model="addForm.houseType" placeholder="请选择">
                            <el-option label="公寓" value="公寓"></el-option>
                            <el-option label="别墅" value="别墅"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1">
                    <el-form-item label="户型" prop="huxing">
                        <br>
                        <el-input v-model="addForm.huxing"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1">
                    <el-form-item label="年收益" prop="income">
                        <br>
                        <el-input-number  v-model="addForm.income"></el-input-number>
                    </el-form-item>
                </el-col>
                <el-col :span="5" :offset="1">
                    <el-form-item label="电梯" prop="elevator">
                        <br>
                        <el-switch
                                :active-value="1"
                                :inactive-value="0"
                                v-model="addForm.elevator"
                                active-color="#13ce66"
                                inactive-color="#ff4949">
                        </el-switch>
                    </el-form-item>
                </el-col>
            </el-row>

                </el-card>


            <el-form-item prop="content">
                <editor v-model="addForm.content"></editor>
            </el-form-item>
        </el-form>
        <div style="margin-top: 20px; text-align: right">
            <el-button @click="add"  size="small" type="primary">添加</el-button>
        </div>

    </div>
</template>
<script>
    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';

    export default {
        name:'null',
        data: function () {
            return {
                countryList:[],
                imageResourceDialogVisible:false,
                videoResourceDialogVisible:false,
                addForm:{
                    priceUnit:"欧元",
                    pictureList:[],
                    videoList:[],
                    picture:"",
                    videos:""
                },
                rules: {
                    title: [
                        { required: true, message: '请填写标题', trigger: 'blur' }
                    ],
                    subTitle: [
                        { required: true, message: '请填写副标题', trigger: 'blur' }
                    ],
                    description: [
                        { required: true, message: '请填写描述', trigger: 'blur' }
                    ],
                    country: [
                        { required: true, message: '请填写国家', trigger: 'blur' }
                    ],
                    price: [
                        { required: true, message: '请输入价格', trigger: 'blur'},
                        { type: 'number', message: '价格必须为数字值'}
                    ],
                    picture: [
                        { required: true, message: '请上传图片', trigger: 'blur' }
                    ],
                    description: [
                        { required: true, message: '请填写描述', trigger: 'blur' }
                    ],
                    country: [
                        { required: true, message: '请选择国家',  trigger: 'change' }
                    ],
                    area: [
                        { required: true, message: '请填写面积', trigger: 'blur' }
                    ],
                    region: [
                        { required: true, message: '请填写区域', trigger: 'blur' }
                    ],
                    floor: [
                        { required: true, message: '请填写楼层', trigger: 'blur' }
                    ],
                    houseType: [
                        { required: true, message: '请选择物业类型', trigger: 'change' }
                    ],
                    huxing: [
                        { required: true, message: '请填写户型', trigger: 'blur' }
                    ],
                    income: [
                        { required: true, message: '请填写收益率', trigger: 'blur' }
                    ],
                    elevator: [
                        { required: true, message: '请选择是否有电梯', trigger: 'blur' }
                    ],
                    content: [
                        { required: true, message: '请填写内容', trigger: 'blur' }
                    ]
                }

            }
        },
        methods:{
            houseList: function () {
                this.$router.push('/house');
            },
            add:function(){
                var self = this;
                self.addForm.picture = JSON.stringify(self.addForm.pictureList);
                var videoIdList = [];
                for(var i=0;i<self.addForm.videoList.length;i++){
                    videoIdList.push(self.addForm.videoList[i].id)
                }
                self.addForm.videos = JSON.stringify(videoIdList);
                this.$refs["addFormRef"].validate((valid) => {
                    if (valid) {

                        axios.post('dayu/house/add',Qs.stringify(self.addForm))
                                .then(function (response) {
                                    if (response.data.resultCode == 0) {
                                        self.$message('添加成功');
                                        self.$router.push('/house');
                                    } else {
                                        self.$message('添加失败');
                                    }
                                })
                                .catch(function (response) {
                                            self.$message('添加失败');
                                        }
                                );
                    } else {
                        self.$message('验证不通过');
                    }
                });
            },
            searchCountry:function(){
                var self = this;
                axios.post('dayu/country/list')
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.countryList = response.data.list;
                            } else {
                                console.log("获取数据失败");
                            }
                        })
                        .catch(function (response) {
                                    console.log(response);
                                }
                        );
            },
            addImage:function(){
                this.imageResourceDialogVisible = true;
            },
            addVideo:function () {
                this.videoResourceDialogVisible = true;
            },
            insertImage:function (value) {
                this.addForm.pictureList.push(value);
                this.imageResourceDialogVisible = false;
            },
            insertVideo:function (value) {
                value.option.width = '200';
                value.option.height = '150';
                this.addForm.videoList.push(value);
                this.videoResourceDialogVisible = false;
            },
            deleteImage:function (index) {
                this.addForm.pictureList.splice(index,1);
            },
            deleteVideo:function (index) {
                this.addForm.videoList.splice(index,1);
            },
        },
        created:function() {
            var self = this;
            self.searchCountry();
        }
    }
</script>

<style>

    .ql-snow .ql-picker {
        line-height:24px!important;
    }
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}

    .picture-item {
        position: relative;
        width: 200px;
        height: 150px;
        border: 2px dashed #dcdfe6;
        text-align: center;
        line-height: 150px;
        border-radius: 5px;
        cursor: pointer;
        float: left;
        margin-right: 10px;
        margin-bottom: 10px;
    }
    .picture-item img {
        width: 200px;
        height: 150px;
        border-radius: 5px;
    }
    .picture-item .picture-item-close {
        margin-top: 0px;
        margin-bottom: 0px;
        padding:0px;
        position: absolute;
        text-align: center;
        width: 200px;
        color: red;
    }
</style>



