<template>
    <div>
        <div class="titleBar">
            <el-row justify="space-between" type="flex">
                <el-col>编辑资讯</el-col>
                <el-col style="width:56px">
                    <el-button :plain="true" @click="newList" size="small" type="success">返回</el-button>
                </el-col>
            </el-row>
        </div>

        <el-form ref="addFormRef" :rules="rules" :model="addForm">
            <el-form-item prop="title">
                <el-input v-model="addForm.title" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item prop="description">
                <el-input type="textarea" v-model="addForm.description" placeholder="简介"></el-input>
            </el-form-item>
            <el-form-item prop="thumbnail">
                <el-upload
                        class="avatar-uploader"
                        action="http://up-z0.qiniu.com"
                        :show-file-list="false"
                        :on-error="handleError"
                        :on-success="handleAvatarSuccess"
                        :data="postData"
                        :before-upload="beforeAvatarUpload">
                    <img v-if="addForm.thumbnail" :src="addForm.thumbnail" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item prop="content">
                <editor v-model="addForm.content"></editor>
            </el-form-item>
        </el-form>
        <div style="margin-top: 20px; text-align: right">
            <el-button @click="edit"  size="small" type="primary">编辑</el-button>
        </div>

    </div>
</template>
<script>
import axios from 'axios';
import Qs from 'qs/dist/qs.js';
import moment from 'moment';


export default {
    name:'null',
    data: function () {
        return {
            postData: {
                token: ''
            },
            addForm:{
                thumbnail:""
            },
            id:0,
            editorOption: {},
            rules: {
                title: [
                    { required: true, message: '请填写标题', trigger: 'blur' }
                ],
                content: [
                    { required: true, message: '请填写内容', trigger: 'blur'},
                ],
                thumbnail: [
                    { required: true, message: '请上传图片', trigger: 'blur' }
                ],
                description: [
                    { required: true, message: '请填写描述', trigger: 'blur' }
                ]
            }
        }
    },
    methods:{
        handleError(res) {
            this.$message("上传失败:"+res);
        },
        handleAvatarSuccess(res, file) {
            this.addForm.thumbnail = 'http://image.dayuxiao.com/'+ res.key;
        },
        beforeAvatarUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isJPG) {
                this.$message.error('上传图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                this.$message.error('上传图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        },
        getToken:function () {
            var self = this;
            axios.post('dayu/qiniu/token')
                    .then(function (response) {
                        self.postData.token = response.data;
                    })
                    .catch(function (response) {
                            }
                    );
        },
        newList: function () {
            this.$router.push('/new');
        },
        detail:function () {
            var self = this;
            axios.post('dayu/new/detail',Qs.stringify({id:self.id}))
                    .then(function (response) {
                        if (response.data.resultCode == 0) {
                            self.addForm = response.data.data;
                        } else {
                            self.$message('查询失败');
                        }
                    })
                    .catch(function (response) {
                                self.$message('查询失败');
                            }
                    );
        },
        edit:function(){
            var self = this;
            this.$refs["addFormRef"].validate((valid) => {
                if (valid) {
                    axios.post('dayu/new/edit',Qs.stringify(self.addForm))
                            .then(function (response) {
                                if (response.data.resultCode == 0) {
                                    self.$message('编辑成功');
                                } else {
                                    self.$message('编辑失败');
                                }
                            })
                            .catch(function (response) {
                                self.$message('编辑失败');
                                    }
                            );
                } else {
                    self.$message('验证不通过');
                }
            });
        },
    },
    created:function() {
        var self = this;
        self.id = self.$route.params.id;
        self.detail();
        self.getToken();
    }
}
</script>

<style >
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 250px;
        text-align: center;
        text-align: -webkit-center;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 250px;
        height: 250px;
        line-height: 250px;
        text-align: center;
    }
    .avatar {
        width: auto;
        height: 250px;
        display: block;
    }
    .titleBar{height: 40px;line-height: 40px;margin-bottom: 20px;}
</style>



