<template>
    <div>
        <div style="margin-bottom: 20px; text-align: right">

            <el-popover
                    ref="popover"
                    placement="top"
                    width="160"
                    v-model="popoverVisible">
                <p><el-input size="small" v-model="addGroupName"  placeholder="分组名"></el-input></p>
                <div style="text-align: right; margin: 0">
                    <el-button size="mini" type="text" @click="popoverVisible = false">取消</el-button>
                    <el-button type="primary" size="mini" @click="doAddGroup">确定</el-button>
                </div>
            </el-popover>

            <el-button size="mini" v-popover:popover>添加分组</el-button>

            <el-button @click="activeName='tab_add'" type="primary" size="mini">添加照片</el-button>
        </div>
        <el-tabs type="border-card" v-model="activeName" @tab-click="switchGroup" tab-position="left" >
            <el-tab-pane name="tab_" label="所有图片">
                <div style="display: flex;flex-wrap: wrap;cursor: pointer">
                    <div v-for="image in imageList" @click="selectImage(image.url)" style="height: 150px;width: 150px; margin-right: 10px; margin-top: 10px;background-color: #eef1f6;">
                        <img :src="image.url" style="width: 150px;height: 120px;">
                        <div style="height: 20px;line-height: 20px;text-align: center;font-size: 12px;overflow: hidden;">{{image.name}}</div>
                    </div>
                </div>
                <el-pagination
                        background
                        layout="prev, pager, next"
                        style="margin-top: 20px;"
                        @current-change="handleImagePageChange"
                        :current-page="imageCurrentPage"
                        :page-size="imagePageSize"
                        @size-change="handleImagePageSizeChange"
                        :total="imageTotalCount"></el-pagination>
            </el-tab-pane>
            <el-tab-pane name="tab_0" label="未分组">
                <div style="display: flex;flex-wrap: wrap;cursor: pointer">
                    <div v-for="image in imageList" @click="selectImage(image.url)" style="height: 150px;width: 150px; margin-right: 10px; margin-top: 10px;background-color: #eef1f6;">
                        <img :src="image.url" style="width: 150px;height: 120px;">
                        <div style="height: 20px;line-height: 20px;text-align: center;font-size: 12px;overflow: hidden;">{{image.name}}</div>
                    </div>
                </div>
                <el-pagination
                        background
                        layout="prev, pager, next"
                        style="margin-top: 20px;"
                        @current-change="handleImagePageChange"
                        :current-page="imageCurrentPage"
                        :page-size="imagePageSize"
                        @size-change="handleImagePageSizeChange"
                        :total="imageTotalCount"></el-pagination>
            </el-tab-pane>
            <el-tab-pane :name="group.tabName" v-for="group in groupList" :key="group.id" :label="group.name">
                <div style="display: flex;flex-wrap: wrap;cursor: pointer">
                    <div v-for="image in imageList" @click="selectImage(image.url)" style="height: 150px;width: 150px; margin-right: 10px; margin-top: 10px;background-color: #eef1f6;">
                        <img :src="image.url" style="width: 150px;height: 120px;">
                        <div style="height: 20px;line-height: 20px;text-align: center;font-size: 12px;overflow: hidden;">{{image.name}}</div>
                    </div>
                </div>
                <el-pagination
                        background
                        layout="prev, pager, next"
                        style="margin-top: 20px;"
                        @current-change="handleImagePageChange"
                        :current-page="imageCurrentPage"
                        :page-size="imagePageSize"
                        @size-change="handleImagePageSizeChange"
                        :total="imageTotalCount"></el-pagination>
            </el-tab-pane>
            <el-tab-pane name="tab_add">
                <span slot="label"><i class="el-icon-circle-plus" style="margin-right: 10px;"></i>添加照片</span>
                <el-select style="margin-bottom: 20px;" size="mini" v-model.number="addImageForm.category" placeholder="请选择">
                    <el-option label="未分组" :value="0"></el-option>
                    <el-option
                            v-for="group in groupList"
                            :key="group.id"
                            :label="group.name"
                            :value="group.id">
                    </el-option>
                </el-select>
                <el-upload
                        class="avatar-uploader"
                        action="http://up-z0.qiniu.com"
                        :show-file-list="false"
                        :on-error="handleError"
                        :on-success="handleAvatarSuccess"
                        :data="postData"
                        :before-upload="beforeAvatarUpload">
                    <img v-if="addImageForm.picture" :src="addImageForm.picture" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
                <div style="margin-top: 20px;text-align: right">
                    <el-button @click="doAddImage">添加</el-button>
                </div>
            </el-tab-pane>
        </el-tabs>



    </div>
</template>

<script>

    import axios from 'axios';
    import Qs from 'qs/dist/qs.js';
    import moment from 'moment';

    export default {
        data: function () {
            return {
                addGroupName:"",
                popoverVisible:false,
                activeName: 'tab_',
                imagePageSize:20,
                imageCurrentPage:0,
                imageTotalCount:0,
                imageList:[],
                groupList:[],
                currentGroupId:"",
                postData:{
                    token:""
                },
                addImageForm:{
                    category:0,
                    picture:"",
                    name:""
                }
            }
        },
        watch:{
            currentGroupId:function (val,oldVal) {
                this.SearchImageWithPage();
            }
        },
        methods:{

            selectImage:function (url) {
                this.$emit("select",url)
            },

            handleImagePageSizeChange:function(size){
                var self = this;
                self.imagePageSize = size;
                self.SearchImageWithPage();
            },

            handleImagePageChange:function(currentPage){
                var self = this;
                self.imageCurrentPage = currentPage;
                self.SearchImageWithPage();
            },

            switchGroup:function (value) {
                var tabName = value.name;
                var values = tabName.split("_");
                if(values[1]!="add"){
                    this.currentGroupId = values[1];
                }
            },

            searchGroup:function(){
                var self = this;
                axios.post('dayu/imageGroup/list')
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.groupList = response.data.list;
                                for (var i=0;i<self.groupList.length;i++){
                                    self.groupList[i].tabName = "tab_"+self.groupList[i].id;
                                }
                            } else {
                                self.$message("获取数据失败");
                            }
                        })
                        .catch(function (response) {
                                    self.$message("获取数据失败");
                                }
                        );
            },

            doAddGroup:function () {
                var self = this;
                axios.post('dayu/imageGroup/add',Qs.stringify({name:self.addGroupName}))
                        .then(function (response) {
                            self.searchGroup();
                            self.popoverVisible = false;
                            self.addGroupName = "";
                        })
                        .catch(function (response) {
                                }
                        );
            },

            getToken:function () {
                var self = this;
                axios.post('dayu/qiniu/token')
                        .then(function (response) {
                            self.postData.token = response.data;
                        })
                        .catch(function (response) {
                                }
                        );
            },
            SearchImageWithPage:function(){
                var self = this;
                axios.post('dayu/image/pageList',Qs.stringify({categoryId:self.currentGroupId,pageSize:self.imagePageSize,pageNo:self.imageCurrentPage}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.imageList = response.data.list;
                                self.imageTotalCount = response.data.totalRecord;
                            } else {
                                self.$message("获取数据失败")
                            }
                        })
                        .catch(function (response) {
                                    self.$message("获取数据失败")
                                }
                        );
            },
            doAddImage:function () {
                var self = this;
                axios.post('dayu/image/add',Qs.stringify({categoryId:self.addImageForm.category,url:self.addImageForm.picture,name:self.addImageForm.name,type:""}))
                        .then(function (response) {
                            if (response.data.resultCode == 0) {
                                self.currentGroupId = self.addImageForm.category;
                                self.SearchImageWithPage();
                                self.activeName = "tab_" + self.addImageForm.category;
                                self.addImageForm = {
                                    category:0,
                                    picture:"",
                                    name:""
                                }
                            } else {

                            }
                        })
                        .catch(function (response) {
                                    self.$message("上传失败")
                                }
                        );
            },
            handleError(res) {
                this.$message("上传失败:"+res);
            },
            handleAvatarSuccess(res, file) {
                this.addImageForm.name = file.name;
                this.addImageForm.picture = 'http://image.dayuxiao.com/'+ res.key;
            },
            beforeAvatarUpload(file) {
                var isImg = false;
                if(file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif'){
                    isImg = true;
                }
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isImg) {
                    this.$message.error('只能上传图片!');
                }
                if (!isLt2M) {
                    this.$message.error('上传图片大小不能超过 2MB!');
                }
                return isImg && isLt2M;
            },
        },
        created:function() {
            var self = this;
            self.SearchImageWithPage();
            self.searchGroup();
            self.getToken();
        }
    }
</script>

<style>
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        width: 100%;
        min-height: 250px;
        text-align: center;
        text-align: -webkit-center;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 250px;
        height: 250px;
        line-height: 250px;
        text-align: center;
    }
    .avatar {
        width: auto;
        height: 250px;
        display: block;
    }

</style>


